import styled, { css, keyframes } from "styled-components";

const neonGlow = keyframes`
  0% {
    text-shadow: 
      0 0 1px #98b9fd,
      0 0 2px #286fff, 
      0 0 3px #286fff;
  }
  100% {
    text-shadow: 
      0 0 2px #98b9fd,
      0 0 4px #286fff, 
      0 0 6px #286fff;
  }
`;

interface TitleProps {
  glowing: boolean;
}

export const Title = styled("h6")<TitleProps>`
  font-size: 2.9em;
  line-height: 1em;
  font-weight: bold;
  height: auto;
  width: auto;
  padding-top: 1rem;
  text-align: center;
  color: #ffffff;
  
  // Apply the neon glow effect based on the 'glowing' prop
  ${props => props.glowing && css`
    color: #fff; // Optional: change the color when glowing
    animation: ${neonGlow} 3s ease-in-out infinite alternate;
  `}

  &:hover {
    color: rgb(40, 111, 255);
  }

  @media only screen and (max-width: 768px) {
    text-align: center;
    margin-top: calc(1rem - 0.5vw);
    margin-bottom: 1.5rem;
    // Apply the neon glow effect for mobile screens
    color: #fff;
    text-shadow: 
      0 0 4px #fff, 
      0 0 10px #286fff,
      0 0 20px #286fff;
  }
`;

export const Content = styled("p")`
  font-size: 1.5em;
  line-height: 1.6em;
  color: #707070;
  height: auto;
  margin-top: 0rem;
  text-align: center;
  margin-bottom: 1rem;

  @media only screen and (max-width: 768px) {
    text-align: center;
    margin-bottom: 1.5rem;
  }
`;

export const Container = styled("div")`
  position: relative;
  max-width: 700px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 0 auto;

  @media only screen and (max-width: 768px) {
    width: 80vw;  /* using viewport width unit */
    padding: 0 .9rem;
  }
`;

export const TextWrapper = styled("div")`
  border-radius: 3rem;
  max-width: 600px;
  margin: 0 auto;
`;
